const IndexPage = () => {

  // twitter();

  let [nyan, setNyan] = useState(null);

  const nyaans = ["", "", "", "", "", "", "", "", ""];

  const randomNyan = () => {
    return nyaans[Math.floor(Math.random() * (nyaans.length))];
  }

  const generateNyan = async () => {
    nyan = await randomNyan();
    setNyan(nyan);
    console.log(nyan);
  }

  if (nyan === null) {
    return (
      <Layout>
        <Seo title="" />
        <p></p>
        <Button variant="contained" endIcon={<Pets />} onClick={generateNyan}>
          
        </Button>
      </Layout>
    );
  } else {
    return (
      <Layout>
        <Seo title="" />
        <p>{nyan}</p>
        <Button variant="contained" startIcon={<Replay />} onClick={generateNyan} style={{ marginBottom: "10px" }}>
          
        </Button>
        <br />
        <Button
          variant="contained"
          href={"https://twitter.com/intent/tweet?hashtags=&text=" + nyan + "%20https://syobonpastel.github.io/random_nya-n/"}
          endIcon={<Twitter />}
          target="_ blank"
        >
          
        </Button>
      </Layout>
    );
  }
}

export default IndexPage
